import Vue from 'vue'

// 路由
import VueRouter from 'vue-router'
Vue.use(VueRouter)

// 1.创建组件
import Home from '../views/Home.vue'
import HttpStatusCode from '../views/HttpStatusCode.vue'
import Encode from '../views/Encode.vue'
import PasswordMaker from '../views/PasswordMaker.vue'
import JsonFormat from '../views/JsonFormat.vue'
import IP from '../views/IP.vue'
// 2.配置路由
const routes = [{
    path: '/home',
    component: Home
  },
  {
    path: '/httpStatusCode',
    component: HttpStatusCode
  },
  {
    path: '/encode',
    component: Encode
  },
  {
    path: '/passwordMaker',
    component: PasswordMaker
  },
  {
    path: '/jsonFormat',
    component: JsonFormat
  },
  {
    path: '/IP',
    component: IP
  },
  {
    path: '*',
    component: Home
  }
]

// 3.实例化 VueRouter
const router = new VueRouter({
  mode: 'history', // hash 模式改为 history history 模式需要后端服务修改些配置文件
  routes
})

export default router