<template>
  <div id="Encode">
    <el-input
      class="code-input"
      type="textarea"
      :autosize="{ minRows: 5, maxRows: 10}"
      placeholder="请输入需要的加解密内容"
      v-model="content"
    ></el-input>
    <el-row type="flex">
      <el-button type="primary" plain @click="Encode('MD5')">MD5 加密</el-button>
      <el-button type="primary" plain @click="Encode('SHA1')">SHA1 加密</el-button>
      <el-button type="primary" plain @click="Encode('SHA224')">SHA224 加密</el-button>
      <el-button type="primary" plain @click="Encode('SHA256')">SHA256 加密</el-button>
      <el-button type="primary" plain @click="Encode('SHA384')">SHA384 加密</el-button>
      <el-button type="primary" plain @click="Encode('SHA512')">SHA512 加密</el-button>
      <el-button type="primary" plain @click="Encode('SHA3')">SHA3 加密</el-button>
      <el-button type="primary" plain @click="Encode('ripemd160')">ripemd160 加密</el-button>
    </el-row>
    <el-row type="flex">
      <el-button-group>
        <el-button type="primary" plain @click="Encode('Base64')">Base64 编码</el-button>
        <el-button type="danger" plain @click="Encode('DecBase64')">Base64 解码</el-button>
      </el-button-group>
      <el-button-group>
        <el-button type="primary" plain @click="Encode('UrlEncode')">UrlEncode</el-button>
        <el-button type="danger" plain @click="Encode('UrlDecode')">UrlDecode</el-button>
      </el-button-group>
    </el-row>
    <el-row v-if="showKey" type="flex">
      <el-input placeholder="请输入密钥" v-model="key">
        <template slot="prepend">密钥key</template>
      </el-input>
    </el-row>
    <el-row type="flex">
      <el-button-group>
        <el-button type="primary" plain @click="KeyEncode('AES', 'enc')">AES 加密</el-button>
        <el-button type="danger" plain @click="KeyEncode('AES', 'dec')">AES 解密</el-button>
      </el-button-group>
      <el-button-group>
        <el-button type="primary" plain @click="KeyEncode('DES', 'enc')">DES 加密</el-button>
        <el-button type="danger" plain @click="KeyEncode('DES', 'dec')">DES 解密</el-button>
      </el-button-group>
      <el-button-group>
        <el-button type="primary" plain @click="KeyEncode('3DES', 'enc')">3DES 加密</el-button>
        <el-button type="danger" plain @click="KeyEncode('3DES', 'dec')">3DES 解密</el-button>
      </el-button-group>
      <el-button-group>
        <el-button type="primary" plain @click="KeyEncode('RC4', 'enc')">RC4 加密</el-button>
        <el-button type="danger" plain @click="KeyEncode('RC4', 'dec')">RC4 解密</el-button>
      </el-button-group>
    </el-row>
    <el-row type="flex">
      <el-button-group>
        <el-button type="primary" plain @click="KeyEncode('Rabbit', 'enc')">Rabbit 加密</el-button>
        <el-button type="danger" plain @click="KeyEncode('Rabbit', 'dec')">Rabbit 解密</el-button>
      </el-button-group>
      <el-button-group>
        <el-button type="primary" plain @click="KeyEncode('RabbitLegacy', 'enc')">RabbitLegacy 加密</el-button>
        <el-button type="danger" plain @click="KeyEncode('RabbitLegacy', 'dec')">RabbitLegacy 解密</el-button>
      </el-button-group>
    </el-row>
    <div class="result">
      <p>
        <b>{{encFunc}}</b>
        {{encode}}
      </p>
    </div>
  </div>
</template>

<script>
import md5 from "js-md5";

export default {
  data() {
    return {
      content: "", // 加密内容
      encFunc: "", // 加密方法
      encode: "", // 加密结果
      showKey: false, // 是否显示密钥输入框
      key: "", // 密钥
      iv: "" // 向量
    };
  },
  methods: {
    Encode(encFunc) {
      const that = this;
      that.showKey = false;
      const content = that.content;
      that.encFunc = encFunc;
      switch (encFunc) {
        case "MD5":
          that.encode = md5(content);
          break;
        case "SHA1":
          that.encode = that.CryptoJS.SHA1(content).toString();
          break;
        case "SHA3":
          that.encode = that.CryptoJS.SHA3(content).toString();
          break;
        case "SHA256":
          that.encode = that.CryptoJS.SHA256(content).toString();
          break;
        case "SHA384":
          that.encode = that.CryptoJS.SHA384(content).toString();
          break;
        case "SHA512":
          that.encode = that.CryptoJS.SHA512(content).toString();
          break;
        case "ripemd160":
          that.encode = that.CryptoJS.RIPEMD160(content).toString();
          break;
        case "SHA224":
          that.encode = that.CryptoJS.SHA224(content).toString();
          break;
        case "Base64":
          that.encFunc = "Base64编码";
          var wordArray = that.CryptoJS.enc.Utf8.parse(content);
          that.encode = that.CryptoJS.enc.Base64.stringify(wordArray);
          break;
        case "DecBase64":
          that.encFunc = "Base64解码";
          var parsedWordArray = that.CryptoJS.enc.Base64.parse(content);
          that.encode = parsedWordArray.toString(that.CryptoJS.enc.Utf8);
          break;
        case "UrlEncode":
          that.encode = encodeURI(content);
          break;
        case "UrlDecode":
          that.encode = decodeURI(content);
          break;
        default:
          break;
      }
    },
    KeyEncode(encFunc, encdec) {
      const that = this;
      if (that.showKey) {
        // 加密
        that.encFunc = encFunc + (encdec == "enc" ? "加密" : "解密");
        switch (encFunc) {
          case "AES":
            that.encode =
              encdec == "enc"
                ? that.CryptoJS.AES.encrypt(that.content, that.key).toString()
                : that.CryptoJS.AES.decrypt(that.content, that.key).toString(
                    that.CryptoJS.enc.Utf8
                  );
            break;
          case "DES":
            that.encode =
              encdec == "enc"
                ? that.CryptoJS.DES.encrypt(that.content, that.key).toString()
                : that.CryptoJS.DES.decrypt(that.content, that.key).toString(
                    that.CryptoJS.enc.Utf8
                  );
            break;
          case "3DES":
            that.encode =
              encdec == "enc"
                ? that.CryptoJS.TripleDES.encrypt(that.content, that.key).toString()
                : that.CryptoJS.TripleDES.decrypt(that.content, that.key).toString(
                    that.CryptoJS.enc.Utf8
                  );
            break;
          case "RC4":
            that.encode =
              encdec == "enc"
                ? that.CryptoJS.RC4.encrypt(that.content, that.key).toString()
                : that.CryptoJS.RC4.decrypt(that.content, that.key).toString(
                    that.CryptoJS.enc.Utf8
                  );
            break;
          case "Rabbit":
            that.encode =
              encdec == "enc"
                ? that.CryptoJS.Rabbit.encrypt(that.content, that.key).toString()
                : that.CryptoJS.Rabbit.decrypt(that.content, that.key).toString(
                    that.CryptoJS.enc.Utf8
                  );
            break;
          case "RabbitLegacy":
            that.encode =
              encdec == "enc"
                ? that.CryptoJS.RabbitLegacy.encrypt(that.content, that.key).toString()
                : that.CryptoJS.RabbitLegacy.decrypt(that.content, that.key).toString(
                    that.CryptoJS.enc.Utf8
                  );
            break;
          default:
            break;
        }
      } else {
        // 显示密钥输入框
        that.showKey = true;
        that.encode = "";
        that.encFunc = "";
      }
    }
  }
};
</script>

<style scoped>
.code-input {
  max-width: 1140px;
}
.el-row {
  margin: 20px;
}
.el-button-group {
  margin-right: 10px;
}
.result {
  max-width: 1140px;
  padding: 8px 16px;
  background-color: #ecf8ff;
  border-radius: 4px;
  border-left: 5px solid #50bfff;
  margin: 20px auto;
}
.result p {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
</style>