
<template>
  <div id="JsonFormat">
    <el-input
      class="code-input"
      type="textarea"
      :autosize="{ minRows: 5, maxRows: 200}"
      placeholder="请输入JSON"
      v-model="jsonStr"
    ></el-input>
    <el-row type="flex">
      <el-button type="primary" plain @click="formatJson()">格式化</el-button>
    </el-row>
    <json-viewer class="json-viewer" :value="jsonData" :expand-depth="5" copyable boxed></json-viewer>
  </div>
</template>

<script>
/* eslint-disable no-useless-escape */
export default {
  data() {
    return {
      jsonStr: "", // JSON 字符串
      jsonData: {}
    };
  },
  methods: {
    formatJson() {
      if (this.jsonStr == "") {
        this.$message({
          message: '请输入 JSON ！',
          type: 'warning'
        });
        return; 
      }
      try {
        this.jsonData = JSON.parse(this.jsonStr);
      } catch (e) {
        this.$message.error('JSON 解析失败');
      }
    }
  }
};
</script>

<style>
.code-input {
  max-width: 1140px;
  margin-bottom: 20px;
}
.json-viewer {
  margin-top: 20px;
  display: flex;
  justify-content: flex-start;
  text-align: left;
}
</style>