<template>
  <div id="passwdmaker">
    <el-row :gutter="20" type="flex" justify="center">
      <el-col :span="2">
        <b>所用字符</b>
      </el-col>
      <el-col :span="8">
        <el-checkbox-group v-model="checkList">
          <el-checkbox-button label="A-Z"></el-checkbox-button>
          <el-checkbox-button label="a-z"></el-checkbox-button>
          <el-checkbox-button label="0-9"></el-checkbox-button>
          <el-checkbox-button label="!@#$%^&*"></el-checkbox-button>
        </el-checkbox-group>
      </el-col>
    </el-row>
    <el-row :gutter="20" type="flex" justify="center">
      <el-col :span="2">
        <b>密码长度</b>
      </el-col>
      <el-col :span="8">
        <el-input-number v-model="length" controls-position="right" :min="1" :max="128"></el-input-number>
      </el-col>
    </el-row>
    <el-row :gutter="20" type="flex" justify="center">
      <el-col :span="2">
        <b>生成</b>
      </el-col>
      <el-col :span="8">
        <el-button type="primary" @click="passwdMake">随机生成密码</el-button>
      </el-col>
    </el-row>
    <el-row :gutter="20" type="flex" justify="center">
      <el-col :span="2">
        <b>密码</b>
      </el-col>
      <el-col :span="8">
       <p>{{password}}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      wordObj: {'A-Z': "ABCDEFGHIJKLMNOPQRSTUVWXYZ", 'a-z': "abcdefghigklmnopqrstuvwxyz", '0-9': "0123456789", '!@#$%^&*': "!@#$%^&*"},
      checkList: ["A-Z", "a-z", "0-9", "!@#$%^&*"], //选中选项
      length: 16, // 密码长度
      password: ""  // 密码
    };
  },
  methods: {
    passwdMake() {
      const that = this;
      if (that.checkList.length == 0) {
        this.$message({
          message: "请选择所用字符!",
          type: "warning"
        });
        return;
      }
      let pasArr = "";
      that.checkList.forEach(element => {
        pasArr += that.wordObj[element]
      });

      let password = "";
      let pasArrLen = pasArr.length;
      for (var i = 0; i < that.length; i++) {
        var x = Math.floor(Math.random() * pasArrLen);
        password += pasArr[x];
      }
      that.password = password
    }
  }
};
</script>

